.hero-section{
   padding-block:73px 250px;

   .main-layout{
      gap:129px;
   }

   .hero-container{
      gap: 30px;

      .slogan{
         font-size: 6.25rem;
         flex: 1;
         
         span{
            color: $color3;
         }
      }
      
      .right-container{
         // flex-basis: 70%;
         width: 445px;
         gap: 79px;
         font-size: 1.125rem;
         font-weight: 500;
         line-height: 28px;
      }

      .decoration{
         width: 50px;
         height: 4px;
         background-color: $color5;
      }
   }

   .left-decoration{
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(-50%, -50%);
      z-index: -1;
   }

   .bottom-decoration{
      position: absolute;
      right: 20%;
      bottom:0;
      transform: translate(50%, 50%);
      z-index: -1;
   }
}