.pros-section{
   background-color: $color2;
   padding-block: 140px;

   .main-layout{
      gap: 125px;
   }

   .left-container{
      gap: 54px;

      .decoration{
         width: 50px;
         height:4px;
         background-color: $color3;
      }
   }

   .cards-container{
      margin-top: 58px;
      gap:32px;
      min-width: 540px;
   }

   .pro-card{
      gap: 23px;

      img{
         align-self: start;
      }

      .text-container{
         line-height: 25px;
         gap:16px;
      }

      h4{
         color:$color3;
         font-size: 1.125rem;
      }
   }
   

   .decoration-right{
      position: absolute;
      right: 0;
      bottom: 0;
      transform: translateX(50%);
   }
}