.app-footer{
   background-color: $color4;
   padding-block: 48px;

   .main-layout{
      justify-content: space-between;
   }

   .left-container{
      gap: 125px;

      .brand-nav{
         gap: 25px;
      }

      p{
         font-size: 0.9375rem;
         line-height: 25px;
      }
   }

   nav{
      gap:40px;

      a{
         font-size: 1.125rem;
      }
   }
   
   .right-container{
      justify-content: space-between;

      .media-icons{
         gap: 16px;
         align-self: end;
      }

      small{
         font-size: 0.9375rem;
      }
   }
}