.gs-section{
   background-color:$color3;
   color: $color2;
   display: grid;
   place-content: center;
   padding-block: 76px;

   .main-layout{
      gap: 260px;
   }

   .contact-btn{
      padding: 10px 32px;
      border: 2px solid $color4;
      border-radius: 10rem;
      align-self: center;
      font-size: 1.125rem;
      font-weight: 500;
   }

   .decoration{
      position: absolute;
      // z-index: -1;
      left: 0;
      bottom: 0;
   }
}