.testimonials-section{
   padding-block: 140px;
   text-align: center;

   .main-layout{
      gap: 56px;
   }

   .section-title{
      span{
         color: $color5;
      }
   }

   .cards-container{
      justify-content: space-between;
      gap: 30px;
   }

   .testimonial-card{
      align-items: center;
      position: relative;

      .quote-mark{
         position: absolute;
         z-index: -1;
      }

      p{
         margin-top: 36px;
         font-size: 0.9375rem;
         line-height: 25px;
         font-weight: 500rem;
      }
      
      .name{
         margin-top: 24px;
         color: $color5;
      }
      
      .circle{
         margin-top: 52px;
         width: 62px;
         height: 62px;
         border: 2px solid $white;
         border-radius: 50%;
      }
   }

   .left-decoration{
      position: absolute;
      left: 0;
      top:0;
      z-index: -1;
   }

   .right-decoration{
      position: absolute;
      right: 0;
      bottom:0;
      z-index: -1;
   }
}